<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide"><div class="col-md-2" style="">
        <div class="hexagon">
          <div class="inner">
            <p data-trn="fi1">Commonality and compatibility design</p>
          </div>
        </div>
      </div></div>
      <div class="swiper-slide"><div class="col-md-23" style="">
        <div class="hexagon">
          <div class="inner">
            <p data-trn="fi2">Second level processing speed</p>
          </div>
        </div>
      </div></div>
      <div class="swiper-slide"><div class="col-md-2" style="">
        <div class="hexagon">
          <div class="inner">
            <p data-trn="fi3">Humane blockchain programming operation</p>
          </div>
        </div>
      </div></div>
      <div class="swiper-slide"><div class="col-md-23" style="">
        <div class="hexagon">
          <div class="inner">
            <p data-trn="fi4">Network resources for free</p>
          </div>
        </div>
      </div></div>
      <div class="swiper-slide"><div class="col-md-2" style="">
        <div class="hexagon">
          <div class="inner">
            <p data-trn="fi5">Improve the scalability of the underlying chain</p>
          </div>
        </div>
      </div></div>
    </div>
    <div class="swiper-button-next"></div>
    <div class="swiper-button-prev"></div>
  </div>
</template>

<script>

  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import _Swiper from 'swiper/dist/js/swiper.js'
  const Swiper = window.Swiper || _Swiper
  export default {
    name: 'carrousel',
    data(){
      return {
        swiperOption: {//以下配置不懂的，可以去swiper官网看api，链接http://www.swiper.com.cn/api/
          // notNextTick是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，<br>　　　　　　　　假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
          notNextTick: true,
          // swiper configs 所有的配置同swiper官方api配置
          autoplay: 3000,
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          allowSilderNext: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          prevButton:'.swiper-button-prev',//上一张
          nextButton:'.swiper-button-next',//下一张
          scrollbar:'.swiper-scrollbar',//滚动条
          mousewheelControl : true,
          observeParents:true,
          // 如果自行设计了插件，那么插件的一些配置相关参数，也应该出现在这个对象中，如下debugger
          debugger: true,
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    mounted(){
      $(function() {
        var swiper1 = new Swiper('.swiper-container', {
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },
        });
      });
    },
    methods: {
      async initData(){
        try{
          const countData = 18801;
          if (countData.status == 1) {
            this.count = countData.count;
          }else{
            this.count = countData;
          }
          this.getAdmin();
        }catch(err){
          console.log('获取数据失败', err);
        }
      }
    },
  }
</script>

<style lang="less">
  html, body, #app {
    height: 100%;
    width: 100%;
  }
  .swiper-container-autoheight, .swiper-container-autoheight .swiper-slide {
    height: 100vh;
  }
  .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
  }
  .swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 8%;
  }
  .swiper-container {
    width: 100%;
    height: 290px;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #000;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
</style>





